import React from 'react';
import { renderToString } from 'react-dom/server';
import { StaticRouter } from 'react-router-dom'; // matchPath 
import { matchRoutes, renderRoutes } from 'react-router-config';
import { Provider } from 'react-redux';
import { ConfigProvider } from 'antd';
import zh_CN from 'antd/lib/locale-provider/zh_CN';
// import zhCN from 'antd/es/locale/zh_CN';
import { createServerStore } from '../store';
import routes from '../routes';

const isProd = process.env.ENV === 'production';

export default (req, res) => {
  const store = createServerStore(req);
  // const promises = []
  // routes.some(route => {
  //     //  matchPath 
  //     const match = matchPath(req.path, route);
  //     if (match) {
  //         promises.push(match);
  //     }
  //     return match;
  // })
  // console.log(promises);

  //  matchRoutes 
  const matchedRoutes = matchRoutes(routes, req.path);
  const promises = [];
  //  promises  loadData 
  matchedRoutes.forEach((item) => {
    // 
    if (item.route.loadData) {
      //  Promise 
      // promises.push(item.route.loadData(store));
      //  Promise.all 
      const promise = new Promise((resolve) => {
        // resolve
        item.route.loadData(store).then(resolve).catch(resolve);
      });
      // puhs promise
      promises.push(promise);
    }
  });

  // ajax
  // console.log(store.getState());

  //  Promise 
  Promise.all(promises).then(() => {
    // 
    console.log(store.getState());

    // 404
    const context = {};
    // 301 
    // Redirect, StaticRoutercontext

    const content = renderToString(
      <ConfigProvider locale={zh_CN}>
        <Provider store={store}>
          <StaticRouter location={req.url} context={context}>
            {renderRoutes(routes)}
          </StaticRouter>
        </Provider>
      </ConfigProvider>,
    );

    let css = '';
    if (isProd) {
      css = `<link href="/status/css/main.css?v=${new Date() * 1}" rel="stylesheet">`;
    }
    // 
    const html = `
                    <!DOCTYPE html>
                    <html>
                        <head>
                            <title>react</title>
                            ${css}
                        </head>
                        <body>
                            <div id="root">${content}</div>
                            <script>
                                window._context = {
                                    state: ${JSON.stringify(store.getState())}
                                }
                            </script>
                            <script src="/client/index.js"></script>
                        </body>
                    </html>
                    `;
    console.log(context);
    if (context.action === 'REPLACE') { // 304
      res.redirect(301, context.url);
    } else if (context.NOT_FOUND) { // 404
      res.status(404);
      res.send(html);
    } else {
      res.send(html);
    }
  });
};
